.modal_order {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	z-index: 49;

	display: flex;
	// display: none;
	align-items: center;
	justify-content: center;

	background-color: rgba(24, 23, 43, .7);

	visibility: hidden;

	@include min1050 {
		background-color: #211F3D;
	}

	&__close_smart {
		display: none;

		&:hover {
			svg {
				line {
					stroke: var(--c-yellow);
				}
			}
		}

		width: rem(40);
		height: rem(40);
		z-index: 2;

		svg {
			width: rem(40) !important;
			height: rem(40) !important;

			@include min540 {
				width: rem(28) !important;
				height: rem(28) !important;
			}
			line {
				stroke: #B2B1BE;
			}
		}

		@include min1050 {
			position: absolute;
			top: rem(68);
			right: rem(50);
			// display: block;
		}

		@include min540 {
			top: rem(24);
			right: rem(15);

			width: rem(28) !important;
			height: rem(28) !important;
		}
	}

	&__close {
		position: absolute;
		top: rem(20);
		right: rem(20);

		@include min1050 {
			display: none;
		}

			&:hover {
				svg {
					line {
						stroke: var(--c-yellow);
					}
				}
			}

			svg {
				line {
					stroke: #B2B1BE;
				}
			}
		}

	&__wrap {
		position: absolute;
		top: 0;
		left: 50%;
		margin-left: rem(-207.5);
		width: rem(415);
		background-color: #211F3D;
		border-radius: rem(5);
		
		display: flex;
		flex-direction: column;

		padding: rem(44) rem(30);

		transform: translateY(-100%);
		transition: var(--transition);

		@include min460 {
			width: 100%;
			min-width: 320px;
			left: 0;
			margin-left: 0;
		}

		

		.title {
			font-size: rem(25);
			color: var(--c-text-gray-light);
			font-weight: 500;
			margin-bottom: rem(20);
			margin-left: auto;
			margin-right: auto;

			@include min540 {
				font-size: rem(20);
				margin-bottom: rem(8);
			}
		}

		.title.mb-5 {
			margin-bottom: rem(5);
		}

		// .flex {
		// 	flex-direction: column;
		// 	align-items: center;

		// 	@include min540 {
		// 		flex-direction: row;
				
		// 		.text {
		// 			margin-right: rem(6);
		// 		}

		// 		.username {
		// 			margin-bottom: 0;
		// 			border-bottom: 0;
		// 			text-decoration: underline;
		// 		}
		// 	}
		// }

		.desktop {
			@include min540 {
				display: none;
			}
		}

		.mobile {
			display: none;

			.text {
				text-align: left;
				span {
					color: var(--c-yellow);
					text-decoration: underline;
					word-break: break-all;
				}
			}

			@include min540 {
				display: block;
			}
		}

		.text {
			color: white;
			text-align: center;

			@include min540 {
				font-size: rem(15);
			}
		}

		.username {
			color: var(--c-yellow);
			margin-top: rem(20);
			font-size: rem(15);
			border-bottom: rem(1.5) solid var(--c-yellow);

			margin-left: auto;
			margin-right: auto;
			align-self: baseline;
		}
		
	}

	&__checked {
		margin-top: rem(30);

		display: flex;
		flex-direction: column;

		.field_checked {
			margin-bottom: rem(10);
			
			&:last-of-type {
				margin-bottom: rem(14);
			}

			.check {
				border-color: var(--c-yellow);
			}
		}

		.field_textarea {
			margin-bottom: rem(28);
		}

		.field_button {
			.button_red_border {
				width: 100%;

				&:hover {
					span {
						color: white;
					}
				}

				span {
					color: var(--c-red);
				}
			}

			.button_red {
				width: 100%;
			}
		}
	}
}


.modal_order.active {
	animation: modal_profile_active 500ms forwards;

	.modal_order__close_smart {
		display: block;
	}

	.modal_order__wrap {
		transition: var(--transition);
		top: 50%;
		transform: translateY(-50%);

		@include min768 {
			// top: 0;
			// transform: translateY(0%);
		}
	}
}