.inline_user_info {
	background-color: rgba(36, 35, 54, .8);
	padding-top: rem(28);
	padding-bottom: rem(36);

	margin-bottom: rem(92);
	position: relative;

	display: flex;
		flex-wrap: wrap;
		align-items: center;

	@include min1200 {
		flex: 0 0 rem(467);
		max-width: rem(467);
		margin-right: rem(20);

		align-items: flex-start;
		align-self: flex-start;
	}

	@include min768 {
		margin-bottom: rem(50);
		margin-right: 0;
		padding-top: rem(32);
		padding-bottom: rem(30);
	}

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: -100%;
		width: 9999em;
		height: 100%;
		background-color: rgba(36, 35, 54, .8);
		z-index: -1;

		@include min1200 {
			display: none;
		}

		@include min768 {
			display: block;
		}
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}

	&__avatar {
		width: rem(160);
		height: rem(160);
		border-radius: 50%;
		margin-right: rem(94);

		@include min1200 {
			margin: 0 rem(30);
			margin-right: rem(20);
			flex: 0 0 rem(100);
			width: rem(100);
			height: rem(100);
		}

		@include min768 {
			flex: 0 0 rem(70);
			width: rem(70);
			height: rem(70);
			margin: 0 rem(10) 0 0;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__col {
		display: flex;
		flex-direction: column;

		@include min1200 {
			margin-top: rem(7);
		}

		@include min768 {
			margin-top: 0;
		}

		.title {
			margin-bottom: rem(15);
			font-size: rem(25);
			font-weight: bold;
			color: white;

			@include min1200 {
				margin-bottom: rem(4);
			}

			@include min768 {
				font-size: rem(20);
			}
		}

		.review {
			display: flex;
			flex-direction: column;
			margin-bottom: rem(10);

			@include min1200 {
				flex-direction: row;
				align-items: center;
			}

			@include min768 {
				margin-bottom: rem(2);
			}

			&__stars {
				margin-bottom: rem(4);

				@include min1200 {
					margin-bottom: 0;
					margin-right: rem(8);
				}

				svg {
					margin-right: rem(6);

					@include min1200 {
						margin-right: rem(4);
					}

					@include min540 {
						width: rem(18) !important;
						height: rem(18) !important;
						margin-right: 0;
					}

					&:last-child {
						margin-right: 0;
					}
				}
			}

			&__text {
				color: var(--c-text-gray);

				@include min768 {
					font-size: rem(13);
				}
			}
		}

		.text {
			font-size: rem(15);
			color: var(--c-text-gray);

			@include min768 {
				font-size: rem(13);
			}
		}
	}

	&__right {
		margin-left: auto;

		@include min1200 {
			margin-left: 0;
			margin-top: rem(28);
			min-width: 0;
			width: 100%;
			// margin-right: rem(36);
		}

		@include min768 {
			margin-top: rem(16);
		}

		.top {
			display: flex;
			flex-wrap: wrap;

			@include min1200 {
				// margin: 0 rem(36);
			}

			.page_prev, .page_next {
				@include min1200 {
					display: none;
				}
			}

			.swiper {
				flex: 0 0 rem(260);
				max-width: rem(260);
				margin: 0 rem(40);

				@include min1200 {
					margin: 0;
					flex: 0 0 100%;
					height: rem(265);
					width: 100%;
					max-width: 100%;
					min-width: 0;
					padding: 0 rem(36);

					.swiper-slide {
						height: auto;
						width: rem(60);
						margin-top: rem(24) !important;
					}
				}

				@include min768 {
					padding: 0;
					flex: 0 0 calc(100% + 30px);
					width: calc(100% + 30px);
					max-width: calc(100% + 30px);
					margin: 0 -15px;
				}

				.item {

					.image {
						width: rem(60);
						height: rem(60);
						border-radius: 50%;
						background-color: var(--c-blue-accent);
					}
				}
			}
		}

		.bottom {
			.page_info {
				bottom: 0 !important;
				display: flex;
				justify-content: center;
				align-items: flex-end;
				margin-top: rem(26);
				color: white;

				@include min1200 {
					margin-top: rem(8);
				}

				span {
					color: white;
					font-weight: 500;
				}
				
				.swiper-pagination-current {
					margin-right: rem(4);
				}

				.swiper-pagination-total {
					font-size: rem(15);
					margin-left: rem(4);
				}
			}
		}

		
	}

	
}

.user_grid {
	display: flex;
	flex-wrap: wrap;

	.user_grid_1 {
		width: 100%;

		@include min1200 {
			order: 1;
		}

		@include min {
			flex: 0 0 100%;
			max-width: 100%;
		}
	}

	.user_grid_2 {
		flex: 0 0 100%;
		// max-width: 100%;

		@include min1200 {
			order: 3;
		}

		@include min991 {
			.table_order {
				&__header {
					>span:nth-child(4) {
						display: flex;
						flex: 0;
						justify-content: flex-end;
					}
				}

				&__row {

					.tb_item:nth-child(3) {
						align-items: baseline;
					}

					.tb_item:nth-child(4) {
						flex: 0 0 rem(72);
						padding: 0;
						
						align-items: baseline;
					
						@include min540 {
							flex: 0 0 rem(56);
							justify-content: flex-end;
						}
					}
				}
			}
		}
	}

	.user_grid_3 {
		flex: 0 0 rem(847);
		max-width: rem(847);
		padding-right: rem(20);

		margin-left: auto;

		@include min1320 {
			flex: 1;

			.sect_row__left {
				flex: 1;
			}
		}

		@include min1200 {
			order: 4;
			max-width: 100%;
			padding-right: 0;

			.sect_row__left {
				max-width: 100%;
				padding-right: 0;
			}
		}
		
	}

	.user_grid_4 {
		flex: 0 0 rem(413);
		max-width: rem(413);

		margin-right: auto;

		@include min1200 {
			order: 2;
			flex: 1;
			max-width: 100%;
			margin-bottom: rem(80);
		}

		@include min {
			flex: 0 0 100%;
			max-width: 100%;
		}
	}
}

.user_page_overlay {

	.header__top {
		padding-top: rem(57);
		padding-bottom: rem(57);
	}

	.bg-overlay {
		background: url(../img/user_page_overlay.jpg) no-repeat top center;
		background-size: cover;
	}

	.tb_item, .chat_box,.user_review_item:nth-child(2n) {
		background-color: rgba(36,35,54,.8);

		// @include min991 {
		// 	background-color: transparent;
		// }
	}

	.table_order__row, .user_review_item {
		@include min991 {
			background-color: rgba(36,35,54,.8);

			&:nth-child(2n) {
				background-color: transparent;
			}
		}
	}

	.chat_box {
		border: 0;

		&__title {
			height: rem(60);
			padding: 0 rem(38);

			flex: 1;
			margin-right: rem(12);

			@include min540 {
				padding: 0 rem(32);
			}
		}

		.tb_box {
			width: rem(60);
			height: rem(60);
		}
	}


	.inline_user_info {
		background-color: transparent;
		margin-bottom: rem(36);

		&__avatar, &__col, &__right {
			position: relative;
			z-index: 1;
		}

		&::before {
			z-index: 0;
		}
	}

	.page_nav {
		.button_yellow_border {
			background-color: var(--c-text-dark);
		}
	}
}