
.breadcrumbs {
	margin-bottom: rem(68);

	display: flex;
	align-items: center;

	@include min540 {
		margin-bottom: rem(40);
		flex-wrap: wrap;
	}

	a, span {
		color: var(--c-text-gray);
		font-size: rem(15);

		@include min540 {
			font-size: rem(13);
			white-space: nowrap;
		}
	}

	a {
		position: relative;
		margin-right: rem(37);

		@include min540 {
			margin-right: rem(17);
		}

		&:hover {
			color: white;
		}

		&::before {
			content: "";
			position: absolute;
			top: rem(3);
			right: rem(-27);
			width: rem(18);
			height: rem(12);
			pointer-events: none;

			background: url(../img/breadcrumbs.svg) no-repeat;
			background-size: 100%;

			@include min540 {
				top: rem(4);

				width: rem(10);
				height: rem(7);
				right: rem(-14);
			}
		}
	}
}

.breadcrumbs__row {
	display: flex;
	justify-content: space-between;
	align-items: center;

	margin-bottom: rem(66);

	@include min540 {
		position: relative;

		.tb_box {
			position: absolute;
			bottom: rem(-54);
			right: 0;
		}
	}

	.breadcrumbs {
		margin-bottom: 0;
	}
}