.sect_review {

}

.review_control {

	svg {
		path {
			transition: var(--transition);
		}
	}

	&:not(:checked) > input {
		display: none;
	}

	&:not(:checked) > label {
		float: right;
		width: rem(35);
		// padding-right: rem(6);
		cursor: pointer;
	}

	input:checked {
		~ label {
			svg {
				path {
					fill: var(--c-yellow) !important;
					stroke: var(--c-yellow) !important;
				}
			}
		}
	}

	&:not(:checked) > label:hover, &:not(:checked) > label:hover ~ label {
		svg {
			path {
				fill: var(--c-yellow) !important;
				stroke: var(--c-yellow) !important;
				}
		}
	}

	input:checked + label:hover,
	input:checked + label:hover ~ label,
	input:checked ~ label:hover,
	input:checked ~ label:hover ~ label,
	label:hover ~ input:checked ~ label {
		svg {
			path {
				fill: var(--c-yellow) !important;
				stroke: var(--c-yellow) !important;
			}
		}
	}
}