.title_h1,
.title_h2 {
	font-weight: 800;
}

.title_h1 {
	font-size: rem(60);

	@include min1050 {
		font-size: rem(50);
	}

	@include min768 {
		font-size: rem(35);
	}
}

.title_h2 {
	font-size: rem(40);

	@include min768 {
		font-size: rem(30);
	}
}


.block_center {
	flex: 0 0 rem(886);
	max-width: rem(886);

	@include min1050 {
		flex: 0 0 100%;
		max-width: 100%;
	}
}

.sect_header {
	display: flex;
	align-items: center;
	justify-content: space-between;

	margin-bottom: rem(100);

	@include min1050 {
		margin-bottom: rem(80);
	}

	.title_h2 {
		color: var(--c-text-gray-light);
	}

	.link {
		display: flex;
		align-items: center;

		&:hover {
			b {
				color: var(--c-pink);
			}
		}

		.link__icon {
			margin-right: rem(20);

			@include min768 {
				margin-right: 0;
			}
		}
	
		b {
			color: var(--c-text-gray-light);

			@include min768 {
				display: none;
			}
		}
	}
}