.chat_box {
	position: relative;
	width: 100%;
	border-radius: rem(2);
	// overflow: hidden;
	border: rem(1.5) solid rgba(178, 177, 190, .15);

	&__title {
		background-color: rgba(141, 139, 167, .1);
		height: rem(60);
		display: flex;
		align-items: center;
		justify-content: center;

		&:hover {
			svg {
				path {
					fill: var(--c-yellow);
				}
			}

			span {
				color: var(--c-yellow);
			}
		}

		svg {
			path {
				fill: #E0DEF4;
			}
		}

		span {
			font-weight: bold;
			color: var(--c-text-gray-light);
			margin-left: rem(10);

			@include min768 {
				font-size: rem(15);
			}
		}

		.desktop {
			@include min540 {
				display: none;
			}
		}

		.mobile {
			display: none;

			@include min540 {
				display: block;
			}
		}
	}

	&__table {
		height: rem(420);

		display: flex;
		flex-direction: column;

		.message__empty {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 100%;

			@include min540 {
				max-width: rem(205);
				margin-left: auto;
				margin-right: auto;
				text-align: center;
			}

			span {
				color: var(--c-text-gray);
				font-size: rem(15);
				margin-bottom: rem(40);
			}
		}
	}

	&__control {
		position: absolute;
		bottom: rem(-1.5);
		left: rem(-1.5);
		height: rem(70);
		display: flex;
		align-items: center;
		width: calc(100% + 3px);
		background-color: #242336;
		z-index: 1;

		border-bottom-left-radius: rem(2);
		border-bottom-right-radius: rem(2);

		.attach {
			margin: 0 rem(25);

			&:hover {
				svg {
					path {
						fill: var(--c-yellow);
					}
				}
			}

			svg {
				path {
					fill: #B2B1BE;
				}
			}
		}

		.text {
			flex: 1;
			color: var(--c-text-gray);
			background-color: transparent;
			margin-right: rem(20);

			@include placeholder {
				color: var(--c-text-gray);
			}
		}

		.send {
			margin: 0 rem(24);
			margin-left: auto;

			&:hover {
				svg {
					transform: translateX(6px);
				}
			}
		}
	}
}