.faq_page {
	margin-left: auto;
	margin-right: auto;

	margin-bottom: rem(150);

	@include min540 {
		margin-bottom: rem(130);
	}

	.field_search {
		position: relative;
		margin-bottom: rem(50);

		@include min540 {
			margin-bottom: rem(30);
		}

		.field {
			border-color: var(--c-yellow);
			height: rem(58);
			padding: 0 rem(80) 0 rem(47);

			color: var(--c-text-gray);

			@include min540 {
				height: rem(54);
				padding: 0 rem(60) 0 rem(20);
			}
			
			@include placeholder {
				color: var(--c-text-gray);
			}
		}

		.button {
			position: absolute;
			top: 0;
			right: 0;

			width: rem(58);
			height: rem(58);
			border-radius: var(--ui-border-4);
			background-color: var(--c-yellow);

			display: flex;
			align-items: center;
			justify-content: center;

			@include min540 {
				width: rem(54);
				height: rem(54);
			}
		}
	}

	
}

.accordion_item {
	margin-bottom: rem(15);

	display: flex;

	background-color: #211F3D;
	border-radius: var(--ui-border-4);

	&__count {
		

		margin: rem(10) 0;

		display: flex;
		align-items: center;
		justify-content: center;

		border-right: rem(1) solid rgba(255,255,255,.2);

		@include max540 {
			flex: 0 0 rem(62);
			max-width: rem(62);
			// height: 100%;
			min-height: rem(36);
		}

		@include min540 {
			border-right: 0;
			margin: 0;
			display: none;
		}

		span {
			font-weight: 500;
			color: white;

			@include min768 {
				font-size: rem(15);
			}
		}
	}

	&__text {
		padding: rem(17) 0;
		margin: 0 rem(13);

		.title {
			font-weight: 500;
			color: white;

			@include max540 {
				span {
					display: none;
				}
			}

			@include min768 {
				font-size: rem(15);
			}
		}

		.text {
			margin-top: rem(16);
			color: white;
			height: 0;
			display: none;

			@include min768 {
				font-size: rem(15);
			}
		}
	}

	&__button {
		margin-left: auto;
		flex: 0 0 rem(54);
		max-width: rem(54);
		background-color: var(--c-yellow);

		display: flex;
		align-items: center;
		justify-content: center;

		border-radius: var(--ui-border-4);
		border: rem(1.5) solid var(--c-yellow);

		@include min540 {
			flex: 0 0 rem(40);
			max-width: rem(40);
		}

		.line {
			font-weight: 500;
			font-size: rem(20);
			color: #211F3D;
		}
	}
}

.accordion_item.active {
	.accordion_item__text {
		.title {
			color: #7E7D9D;
		}
		.text {
			display: block;
			animation: accordion 500ms forwards;
		}
	}

	.accordion_item__button {
		background-color: transparent;
		.line {
			span {
				display: none;
			}
			&::before {
				content: "-";
				color: var(--c-yellow);
			}
		}
	}
}

@keyframes accordion {
	0% {
		display: block;
	}
	10% {
		height: 10%;
	}
	100% {
		height: 100%;
	}
}

.message_block {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(160);

	@include min1050 {
		margin-bottom: rem(80);
	}

	&__header {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: rem(70);

		@include min540 {
			margin-bottom: rem(50);
		}

		.title {
			font-size: rem(30);
			color: var(--c-text-gray-light);
			margin-bottom: rem(28);

			@include min1050 {
				text-align: center;
				font-size: rem(25);
				margin-bottom: rem(15);

				span {
					display: none;
				}
			}
		}

		.text {
			color: var(--c-text-gray-light);

			@include min1050 {
				text-align: center;
			}
		}

		.desktop {
			@include min540 {
				display: none;
			}
		}

		.mobile {
			display: none;
			@include min540 {
				display: block;
			}
		}
	}

	&__form {
		display: flex;
		align-items: center;

		@include min1050 {
			flex-wrap: wrap;
			justify-content: center;
			// flex-direction: column;

		}

		.field_button {
			width: 100%;
			display: flex;
			justify-content: center;

			.button_yellow {
				margin-right: rem(14);
			}

			.button_yellow_border {
				flex: 0 0 auto;
				max-width: 100%;
			}

			@include min540 {
				// flex-direction: column;
				// align-items: center;
				flex-wrap: wrap;
				.button_yellow {
					margin-bottom: rem(10);
				}
				.button_yellow, .button_yellow_border {
					flex: 0 0 100%;
					max-width: 100%;
				}
			}

			
		}

		.review_control {
			margin-right: rem(20);

			@include min1050 {
				margin-right: 0;
				margin-bottom: rem(20);
				width: 100%;
				display: flex;
				flex-direction: row-reverse;
				justify-content: center;
			}
		}

		.field {
			border: 0;
			height: rem(54);
			padding: 0 rem(30);
			color: rgba(255,255,255,.8);
			background-color: rgba(178, 177, 190, .3);
			border-radius: rem(2);
			margin-right: rem(20);

			@include min1050 {
				max-width: rem(500);
				margin-right: 0;
				height: rem(54);
				margin-bottom: rem(20);
			}

			@include min540 {
				margin-bottom: rem(10);
			}

			&:first-child {
				flex: 0 0 rem(305);
				max-width: rem(305);

				@include min1050 {
					flex: 0 0 auto;
					max-width: rem(500);
				}
				
				@include min540 {
					flex: 0 0 100%;
					max-width: 100%;
				}
			}

			&:nth-child(2) {
				flex: 1;

				@include min1050 {
					flex: 0 0 auto;
					max-width: rem(500);
				}

				@include min540 {
					flex: 0 0 100%;
					max-width: 100%;
				}
			}

			@include placeholder {
				color: rgba(255,255,255,.8);
			}
		}

		.button_yellow_border {
			justify-content: center;

			&:hover {
				span {
					color: var(--c-text-dark);
				}
			}

			span {
				color: var(--c-yellow);
			}
			@include min1050 {
				flex: 0 0 rem(500);
				max-width: rem(500);
			}

			@include min540 {
					flex: 0 0 100%;
					max-width: 100%;
				}
		}
	}
}