.new_items {
	display: flex;
	flex-wrap: wrap;

	.new_item {
		margin-right: rem(20);
		margin-bottom: rem(20);
		width: calc((100% - 40px) / 3);

		@include min1050 {
			width: calc((100% - 20px) / 2);

			&:nth-child(2n) {
				margin-right: 0;
			}
		}

		@include max1050 {
			&:nth-child(3n) {
				margin-right: 0;
			}
		}

		@include min768 {
			width: 100%;
			margin-right: 0 !important;

			
		}

		@include min540 {
			margin-bottom: rem(40);
			padding-bottom: rem(40);
			border-bottom: rem(1.5) solid #333238;
			
			&:last-child {
				border-bottom: 0;
				padding-bottom: 0;
				margin-bottom: 0;
			}
		}

		
	}
}

.text_col {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	margin: rem(150) 0 rem(80) 0;

	.title {
		font-size: rem(30);
		color: white;
		text-align: center;
		margin-bottom: rem(28);

		@include min540 {
			font-size: rem(25);
			margin-bottom: rem(15);
		}
	}

	.text {
		color: white;
		text-align: center;

		a {
			font-weight: 500;
		}
	}
}