.page_not_fount {

	// background-color: #0A0A11;
	background: #0A0A11 url(../img/404.svg) no-repeat top center;
	background-size: cover;

	@include min1050 {
		background: #0A0A11 url(../img/404_table.svg) no-repeat top center;
		background-size: cover;
	}

	@include min {
		background: #0A0A11 url(../img/404_mobile.svg) no-repeat top center;
		background-size: cover;
	}

	.header__top {
		@include min {
			padding-bottom: 0;
		}
	}

	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		
		margin-top: rem(52);
		margin-bottom: rem(100);

		@include min768 {
			margin-bottom: rem(60);
		}


		.title {
			margin-bottom: rem(445);
			font-size: rem(30);
			color: white;
			text-transform: uppercase;

			@include min1050 {
				// margin-bottom: rem();
			}

			@include min768 {
				margin-bottom: rem(182);
				font-size: rem(11);
			}
		}
		
		.button_yellow {
			width: rem(392);

			@include min768 {
				width: 100%;
			}
		}
	}
}