.product_game {

	.sect_header {
		margin-bottom: rem(112);

		@include min1320 {
			margin-bottom: rem(80);
		}

		@include min {
			margin-bottom: rem(46);
		}

		.title_h2 {
			color: var(--c-text-gray-light);
		}
	}

	&_box {
		display: flex;
		flex-wrap: wrap;

		margin-bottom: rem(150);

		@include min1320 {
			margin-bottom: rem(90);
		}

		.content {
			flex: 0 0 rem(847);
			max-width: rem(847);

			margin-bottom: rem(80);

			@include min1320 {
				flex: 0 0 100%;
				max-width: 100%;

				order: 0;
			}

			@include min {
				order: 2;
				margin-bottom: rem(42);
			}

			&__top {
				margin-bottom: rem(68);
				width: 100%;
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				@include min768 {
					margin-bottom: rem(34);
				}

				.review_box {
					margin-left: rem(28);

					@include min540 {
						margin-left: rem(10);
					}
					
				}

				.control {
					margin-left: auto;

					display: flex;
					flex-wrap: wrap;
					align-items: center;

					@include min768 {
						flex: 0 0 100%;
						max-width: 100%;
						margin-top: rem(24);
					}

					.tb_box {
						margin-right: rem(10);

						&:last-child {
							margin-right: 0;
						}
					}
				}
			}

			&__info {
				display: flex;
				flex-wrap: wrap;
				// justify-content: space-between;

				.left {
					display: flex;
					flex-direction: column;
					margin-right: rem(106);

					@include min768 {
						margin-right: 0;
						margin-bottom: rem(24);
					}

					.title {
						margin-bottom: rem(40);
						font-size: rem(17);
						font-weight: 500;
						color: var(--c-text-gray);

						@include min768 {
							margin-bottom: rem(24);
						}
					}

					p {
						color: var(--c-text-gray-light);
						line-height: rem(24);
						font-size: rem(15);
					}
				}

				.right {
					.title {
						margin-bottom: rem(40);
						font-size: rem(17);
						font-weight: 500;
						color: var(--c-text-gray);
					}

					ul {
						li {
							display: flex;
							// justify-content: space-between;
							margin-bottom: rem(23);

							&:last-child {
								margin-bottom: 0;
							}

							span {
								&:first-child {
									font-size: rem(15);
									color: var(--c-text-gray);

									flex: 0 0 rem(170);
									max-width: rem(170);
								}
								&:last-child {
									font-size: rem(15);
									color: var(--c-yellow);
								}
							}
						}
					}
				}
			}

		}

		.bay_panel {
			flex: 0 0 rem(413);
			max-width: rem(413);
			margin-left: rem(20);

			@include min1320 {
				order: 2;
				margin-left: 0;
				margin-right: rem(72);
			}

			@include min991 {
				margin-right: rem(20);
			}

			@include min {
				flex: 0 0 100%;
				max-width: 100%;
				margin-right: 0;
				margin-bottom: rem(72);
				order: 1;
			}
		}

		.chat_box {
			flex: 0 0 rem(847);
			max-width: rem(847);

			position: relative;
			top: rem(-60);

			@include min1320 {
				top: 0;
				flex: 1;
				max-width: 100%;

				order: 3;
			}

			@include min {
				flex: 0 0 100%;
				max-width: 100%;
				
			}
		}
	}

	
}

.sect_row {

	display: flex;
	flex-wrap: wrap;

	margin-bottom: rem(146);

	@include min768 {
		margin-bottom: rem(130);
	}

	.block_center {
		margin-left: auto;
		margin-right: auto;
	}

	&__left {
		flex: 0 0 rem(847);
		max-width: rem(847);
		padding-right: rem(20);
	}

	&__right {
		flex: 1;
	}
		
		

		.table_order {
			width: 100%;
		}

		&__header {
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 100%;
			margin-bottom: rem(80);

			@include min768 {
				margin-bottom: rem(70);
			}

			.title_h2 {
				color: var(--c-text-gray-light);
			}

			.desktop {
				@include min768 {
					display: none;
				}
			}

			.mobile {
				display: none;

				@include min768 {
					display: block;
				}
			}

			.link {
				display: flex;
				align-items: center;

				@include min768 {
					display: none;
				}

				&:hover {
					b {
						color: var(--c-yellow);
					}

					svg {
						path {
							fill: var(--c-yellow);
						}
					}
				}

				svg {
					path {
						fill: #E0DEF4;
					}
				}

				b {
					color: var(--c-text-gray-light);
					margin-right: rem(7);
				}
			}
		}

		.custom_select {
			width: auto;
			margin-bottom: rem(30);
		}
		
		.user_review_item, .page_nav {
			flex: 0 0 rem(847);
			max-width: rem(847);

			@include min1320 {
				flex: 0 0 100%;
				max-width: 100%;
			}
		}
		
		.page_nav {
			margin-top: rem(60);
			justify-content: center;
		}
	}


.user_info {
	display: flex;
	align-items: center;

	&__image {
		position: relative;
		margin-right: rem(13);

		width: rem(54);
		height: rem(54);
		border-radius: 50%;
		border: rem(0.7) solid var(--c-yellow);

		display: flex;
		align-items: center;
		justify-content: center;

		

		img {
			width: 65%;
		}

		.status {
			position: absolute;
			bottom: 0;
			right: rem(-3);
			width: rem(21);
			height: rem(21);
			border-radius: 50%;
			background-color: var(--c-blue-accent);
		}
	}

	&__right {
		display: flex;
		flex-direction: column;

		.title {
			position: relative;
			color: var(--c-text-gray-light);
			font-size: rem(15);
			align-self: baseline;

			.online {
				position: absolute;
				top: rem(-4);
				right: rem(-10);
				width: rem(8);
				height: rem(8);
				border-radius: 50%;
				background-color: #27AE60;
			}
		}

		.date {
			margin-top: rem(2);
			font-size: rem(13);
			color: var(--c-text-gray);
		}

		.subtext {
			display: flex;
			align-items: center;

			span {
				font-size: rem(13);
				color: var(--c-text-gray);
				margin-right: rem(5);
			}
		}
	}
}

.review_box {
	display: flex;
	flex-direction: column;

	.flex {
		svg {
			margin-right: rem(4);
		}
	}

	&__text {
		margin-top: rem(2);

		span {
			font-size: rem(13);
			color: var(--c-text-gray);
		}
	}
}

.bay_panel {
	padding: rem(40) rem(26);
	border-radius: rem(2);
	background-color: #211F3D;

	display: flex;
	flex-direction: column;

	@include min {
		padding: rem(20) rem(12);
	}

	&__message {
		margin-top: rem(45);
		margin-bottom: rem(90);
		padding: rem(58) 0;
		border-top: rem(1) solid rgba(178, 177, 190, .3);
		border-bottom: rem(1) solid rgba(178, 177, 190, .3);

		display: flex;
		flex-direction: column;
		align-items: center;

		.title {
			font-size: rem(25);
			font-weight: 500;
			color: var(--c-text-gray-light);
			margin-bottom: rem(11);
		}

		.text {
			color: var(--c-text-gray-light);
			text-align: center;
			

			span {
				color: var(--c-yellow);
				text-decoration: underline;
			}
		}
	}

	&__button {
		margin-top: auto;
		button, a {
			width: 100%;
		}
	}

	&__title {
		display: flex;
		align-items: center;
		justify-content: center;
		padding-bottom: rem(40);
		margin-bottom: rem(34);
		border-bottom: rem(1) solid rgba(178, 177, 190, .3);

		@include min {
			margin-bottom: rem(22);
			padding-bottom: rem(20);
		}

		span {
			color: white;

			@include min768 {
				font-size: rem(15);
			}
		}

		svg {
			margin-left: rem(5);
			margin-right: rem(10);

			@include min540 {
				width: rem(15) !important;
				height: rem(15) !important;
			}
		}
	}

	&__field {

		.field {
			opacity: 0.5;
			margin-bottom: rem(34);

			@include min {
				margin-bottom: rem(24);
			}
		}

		.calc_control {
			margin-left: auto;
			margin-right: auto;
			margin-bottom: rem(40);
		}

		.result {
			display: flex;
			align-items: flex-end;
			margin-bottom: rem(40);

			@include min {
				margin-bottom: rem(34);
			}

			@include min540 {
				align-items: center;
			}

			.first {
				color: var(--c-text-gray);
				font-weight: 500;
				margin-right: rem(24);
			}

			.count {
				font-size: rem(30);
				font-weight: bold;
				color: var(--c-text-gray-light);
				margin-right: rem(10);

				display: flex;
				align-items: center;
				align-self: baseline;

				@include min {
					font-size: rem(17);
				}

				@include min540 {
					margin-top: rem(5);
				}

				svg {
					margin-left: rem(5);

					@include min {
						width: rem(20) !important;
						height: rem(20) !important;
					}
				}
			}

			.last {
				font-size: rem(13);
				color: var(--c-text-gray);

				@include min {
					text-align: right;
				}

				span {
					text-decoration: underline;
				}
			}
		}

		.custom_select {
			margin-bottom: rem(18);
			

			.ss-single-selected {
				height: rem(54) !important;
			}

			.ss-single-selected,.ss-content .ss-list {
				background-color: #211F3D !important;
			}
		}

		.button_yellow_border {
			width: 100%;
			justify-content: center;
		}
	}
}

.user_review_item_row {
	@include min540 {
		margin-left: -15px;
		margin-right: -15px;
	}
}

.user_review_item {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	@include min {
		background-color: #242336;
		padding: rem(26) rem(18);
	}

	@include min540 {
		padding: rem(20) rem(15);
	}

	&:nth-child(2n) {
		background-color: #242336;

		@include min {
			background-color: transparent;
		}
	}

	&__left {
		padding: rem(26) rem(18);

		flex: 0 0 rem(305);
		max-width: rem(305);

		@include min {
			order: 1;
			flex: 0 0 100%;
			max-width: 100%;
			padding: 0;
		}

		.user_info__image {
			@include min540 {
				width: rem(40);
				height: rem(40);
			}
		}

		
	}

	&__right {
		display: flex;
		flex: 1;
		flex-direction: column;

		padding-right: rem(45);

		@include min {
			padding-right: 0;
			flex: 0 0 100%;
			max-width: 100%;
			order: 0;
			margin-bottom: rem(15);
		}

		.flex {
			margin-bottom: rem(3);

			svg {
				margin-right: rem(4);

				&:last-child {
					margin-right: 0;
				}
			}
		}

		p {
			font-size: rem(13);
			color: var(--c-text-gray-light);
		}
	}
}