.modal_profie {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 51;
  visibility: hidden;
}

.modal_profie__wrap {
  position: absolute;
  max-width: 37.5rem;
  width: 100%;
  top: 0;
  transform: translateY(-100%);
  left: 50%;
  background-color: #211F3D;
  border-radius: 0.3125rem;
  margin-left: -18.75rem;
  padding-bottom: 3.75rem;
  transition: var(--transition);
  overflow-y: auto;
}

@media (max-width: 767px) {
  .modal_profie__wrap {
    left: 0;
    height: 100%;
    max-width: 100%;
    margin-left: 0;
    padding-top: 5rem;
  }
}

.modal_profie__wrap .js--modal-close {
  position: absolute;
  top: 0.9375rem;
  right: 0.9375rem;
}

.modal_profie__tab {
  position: relative;
  margin-left: 5.3125rem;
  margin-right: 5.3125rem;
  margin-bottom: 2rem;
  display: flex;
}

@media (max-width: 767px) {
