  }
}

.modal_profie__tab button {
  position: relative;
  flex: 0 0 50%;
  max-width: 50%;
  padding: 2rem 0;
  display: flex;
  justify-content: center;
  font-size: 1.4375rem;
  font-weight: 500;
  color: var(--c-text-gray-light);
}

@media (max-width: 767px) {
